import React ,{ Component } from 'react';
import Link from 'next/link';
import Router from 'next/router';

import Image from '../../UI/Image/Image';
import Button from '../../UI/Button/Button';
import Title from '../../UI/Title/Title';
import Favorite from '../../../pages/favorite';


class FavoriteItem extends Component{
    constructor(props){
        super(props);        
        this.deleteFavoriteItem = this.deleteFavoriteItem.bind(this);

    }

    async deleteFavoriteItem() {
        // console.log("called" ,this.props.name);
        // const res=  await fetch('http://localhost:8080/delete?name='+this.props.name)
        // const json = await res.json()
        await Router.push('/')
        //return(json);
      }
    render(){    
        return(
            <div className="row">
            <Title username={this.props.name}/>
            <Image image={this.props.image}/>     
            <a onClick={()=>this.deleteFavoriteItem()}><Button>fa fa-fw fa-trash</Button></a>
        
            <style jsx>
            {`
                img {
                    max-width: 100%;
                    height: auto;
                }
                a{
                    position: absolute;
                    
                }
            
            `}    
            </style>
            <hr/>
    </div>
    );
}}



export default FavoriteItem;